h4 {
 color: #FFF;
 font-weight: 400;
 font-size: 2em;
}
.isloadtrack{
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
}
.isloadtrack label{
    margin-left: 1em;
}
.cardIntegral{
    max-width: 35em;
    margin-bottom: 2em;
}

.btncode{
    width: 25%;
    font-size: 1.1em;
    padding:6px;
    background : #be1e2d;
    color: #FFF;
    border: 1px solid rgb(135, 129, 144);
    border-left: 0px;
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
}
.btncode:focus{
    outline: none;
    background : #ae0e1d;
}
.btncode:hover{
    cursor: pointer;
    background : #ae0e1d;
}
.inpcode{
    width: 60%;
    font-size: 1.1em;
    padding:6px;
    border: 1px solid rgb(135, 129, 144);
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
    color: #222;
    margin-left: 0px;
    background: #EEE;
    padding-left:1em
}
.inpcode:focus{
    border: 1px solid #222;
    outline: none;
    background: #FFF
}
