.mmback{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.6;
  }
.mmodal {
    position: fixed;
    background: #fff;
    left: 30%;
    margin: 0 auto;
    opacity: 1;
    overflow: hidden;
    padding: 0;
    top: 15%;
    width: 40%;
    max-width: 80%;
    border-radius: 0.5em;
    z-index: 99;
}
.mm-header {
    background-color:#be1e2d;
    color: #FFF;
    height:1.1em;
    font-weight: 500;
    padding: 20px 20px 20px 20px;
    overflow: hidden;
    font-size: 1.1em;
    width: 100%;
}
.mm-body{
    padding: 20px 20px 20px 20px;
}
.mm-body p {
    color: #222;
    font-weight: 500;
}
.mm-body button {
    border: 0;
    position: relative;
    float:right;
    background: #bababa;
    color: #111111;
    padding: 9px 18px;
    border-radius: 9px;
    font-size: 1.1em;
    margin: 4px;
}
.mm-body button:hover {
    background: #cacaca;
}

@media (max-width: 600px) {
    .mmodal {
        width: 80%;
        left: 5%;
    }   
}