.cls-1{isolation:isolate;}
.cls-2{mix-blend-mode:multiply;opacity:0.4;}
.cls-3{fill:#1b95d2;}
.cls-4{fill:#24336b;}

.cont387item{
    width: 300px;
    height: 90px;
    position: relative;
}

.cont387item svg {
    position: absolute;
    left:0px;
    top:0px;
    z-index: 10;
}

.cont387item .txcolor387{
    position: absolute;
    margin:10px;
    margin-left: 35px;
    width:250px;
    left: 0;
    top: 0;
    color: #FFF;
    font-size: 1.1em;
    z-index: 20;
}

.txcolor387 .textin{
    display: flex;
    width: 100%;
    height: 60px;
    justify-content: left;
    align-items: center;
}