.sec809ic{
    background:#FFF;
    box-sizing: border-box;
    color:#222;
    display: flex;
    justify-content: center;
    margin-top: -50px;
    min-height:70px;
    padding:1em;
    width:100%;
}
.sec809adp {
    margin-top: 20px;
    margin-right: 10em;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 700px;
    align-items: center;
    justify-content:center;
    align-items: center;
}

.sec809middle{
    width: 300px;
    height: 10px;
    position: relative;
}
.sec809middleint{
    position: absolute;
    left: -250px;
    top: -70px;
}

@media screen and (max-width: 600px){
    .sec809adp{
      display: flex;
      flex-direction:column;
      margin-right: 0em;
      width: 370px;
    }
    .sec809middleint{
        left: -120px;
        height: 30px;
        top:-80px;
    }    
}