.hnxs{
  font-size: 1em
}
.hxs{
  font-size: 1em
}
.optcontact{
  border: solid 1px;
  padding: 5px;
  padding-left: 8px;
  padding-right: 10px;
  border-radius: 10px;
  font-style: bold;
}

.NavMain {
    align-items: center;
    background: #24336b;
    color: white;
    display: flex;
    flex-direction: row;
    font-size: calc(10px + 2vmin);
    justify-content: center;
    margin-bottom: 1em;
    margin-top: 1em;
    max-width:100%;
    min-height: 4vh;
    overflow-x: hidden;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    width: 100%;
}
.NavMain a{
    color:#FFF;
    font-size: 0.7em;
    font-weight: 200;
    margin: 0 0.8em;
    text-decoration: none;
  }
.NavMain a:hover{
    color:#999;
    text-decoration: dashed;
}

.nmlo{
    margin-right: 4em;
    width : 7em;
}
.nmli{
  color: #FFF;
  display: flex;
  align-items: center;
  align-content: center;
}

.nmlo img{
    width: 100%;
}

.nmisan{
  border:0px;
  cursor: pointer;
  padding:0px;
  padding-right: 10px;
}


@media (max-width: 600px) {
  .nmli{
    display: none;
    color: white;
    position: fixed;
    top:-10px;
    right: 0;
    height: 100vh;
    margin-top:80px;
    width: 100%;
    background-color: blue;
  }
  .NavMain {
    transition: 0.3s;
    background:#EEE;
    color: #000;
    justify-content: flex-start;
    margin-top: 0;
    max-height: 60px;
    overflow-y: hidden;
    margin: 0;
    padding: 4px;
    box-sizing: border-box;
  }
  .hxs{display:none}
  .nmisan{display:flex; margin-left: 0.5em; margin-right:1em}
}
@media (min-width: 601px) {
  .hnxs{display:none}
}