.layPage{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background : #24336b;
}
.layMain {
    /* Alineamiento del eje secundario align-items: center; */
    background: #24336b;
    color:#FFF;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; /* Permite crear nueva linea */
    justify-content: center;
    font-weight: 200;
    max-width: 100%;
    overflow-x: hidden;
    padding: 0.5em;
}
