.container {
    background: #EAEAEA;
    border-radius: 12px;
    color: #333;
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    margin-top: 2em;
}

h2 {
    font-size: 26px;
    margin: 20px 0;
    text-align: center;
}
.responsive-table{
    padding:0.5em;
}
.responsive-table li {
    border-radius: 3px;
    padding: 25px 30px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
}
.responsive-table .table-header {
    background-color: #f6c03c;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.03em;
    font-weight: 600;
}
.responsive-table .table-row {
    background-color: #fff;
    box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);
    color : #333;
}
.responsive-table .col-1 {
    flex-basis: 10%;
}
.responsive-table .col-2 {
    flex-basis: 50%;
}
.responsive-table .col-3 {
    flex-basis: 25%;
}
.responsive-table .col-4 {
    flex-basis: 25%;
}
@media all and (max-width: 600px) {
    .responsive-table .table-header {
        display: none;
   }
    .responsive-table li {
        display: block;
   }
    .responsive-table .col {
        flex-basis: 100%;
   }
    .responsive-table .col {
        display: flex;
        padding: 10px 0;
   }
    .responsive-table .col:before {
        color: #333;
        font-weight: 700;
        padding-right: 10px;
        content: attr(data-label);
        flex-basis: 35%;
        text-align: left;
   }
}
