.confoo{
    border-top: solid 4px #354274;
    margin-top: 0em;
    padding-top: 2em;
    background: #24336b;
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 200px;
    width: 100%;
    justify-content:space-between;
}
.fotitle{
    color: #a0a0a0;
    font-size: 2.5em;
}
.foocol{
    padding: 1em;
    min-width: 20%;
    box-sizing: border-box;
}
.foimgcir { 
    position: absolute; 
    clip: rect(0, 200px, 200px, 100px);
    left: -100px;
     /* clip: shape(top, right, bottom, left);*/
}
.focol1{
    width: 10%;
    min-width: 100px;
    display: flex;
    align-self:flex-start;
    margin-top: 2em;
    justify-content:flex-start;

}

.foul1{
    padding:0;
    list-style-type: none;
    color: #FFF;
    /*font-family: 'Libre Franklin', sans-serif;*/
    /*font-family: 'Quicksand', sans-serif; */
    min-width: 320px;
    font-size: 1.2em;
    font-weight: 500;
    font-family: 'Quicksand', sans-serif;;
}


.foul1 li {
    padding-top: 0.5em;
    font-family: 'Libre Franklin', sans-serif;
    align-items: center;
    justify-content:flex-start;
    display: flex;
}

.foul1 li svg {
    padding-right: 0.5em;
}
.foform{
    background: #FFF;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    padding: 2em;
    width:100%;
    min-width: 400px;
    box-sizing: border-box;

}
.foform label {
    font-size:1.1em;
    font-weight:500;
    padding-top: 4px;
    color: #24336b;
}
.foform input, textarea{
    background: #e6e6e6;
    border-radius: 5px;
    border: 0;
    margin-bottom: 6px;
    margin-top: 4px;
    resize: none;
    padding:0.3em;
}
.foform button {
    width: 13em;
    background: #1b95d2;
    color: #FFF;
    margin-top: 2em;
    padding: 0.4em;
    border: 0;
    border-radius: 5px;
    align-self: flex-end;
}
.foform button:hover {
    background: #0373ab;
    cursor: pointer;
}
.listcontact{
    margin: 4px 10px;
    color: #FFF;
    display: flex;
    justify-content: center;
    align-items: center;
}
.listcontact a {
    margin-left: 10px;
    display: flex;
    justify-content: center;
    color: #FFF;
    text-decoration: none;
    align-self: center;
}
.listcontact a:hover{
    color: lightblue;
    transition: color 0.2s;
    margin-left: 5px;
    transition: all 0.3s;
}
.listcontact span {
   align-self: center;
   align-items: center;
   display: flex;
}



@media (max-width: 600px) {
    .foform{
        min-width: 320px;
        padding: auto;
        box-sizing: border-box;    
    }
    .foocol{
        width: 100%;
    }
}       