.btn-whatsapp {
    padding: 0px;
    background-color: #eee;
    opacity: 0.8;
    display: block;
    width: 51px;
    height: 51px;
    color: #fff;
    position: fixed;
    right: 10px;
    bottom: 52px;
    border-radius: 50%;
    line-height: 80px;
    text-align: center;
    z-index: 999;
}

.btn-whatsapp img {
    width: 45px;
    height: 45px;
    padding: 0px;
    border: 0px;
    margin: 0px;
    position: absolute;
    top: 3px;
    left: 3px;
}
